.footer {
  height: 200px;
  background-image: url(../images/footer/Footer-photo-desktop.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

@include onTablet {
  .footer {
    height: 400px;
  }
}

@include onDesktop {
  .footer {
    height: 460px;
  }
}
